@import url(https://fonts.googleapis.com/css2?family=Mukta&display=swap);
body{font-family:"Mukta", sans-serif;background-color:#f4fdff}.hidden{display:none}ul{list-style-type:none}.buttons{display:flex;justify-content:center;grid-gap:20px;gap:20px}.button{font-family:"Mukta", sans-serif;padding:3px 20px;background-color:#bdffd8;border:none;border-radius:20px;color:#4eb679;outline:none;box-shadow:rgba(189,255,216,0.5) 0 7px 29px 0;transition:all 0.5s}.button:hover{background-color:#bddaff;box-shadow:rgba(189,242,255,0.5) 0 7px 29px 0;color:#4e6ab6}select{background-color:#bdffd8;border:none;border-radius:20px;outline:none;color:#4eb679;padding:3px 20px}.listOfGoods{display:flex;align-items:center;flex-direction:column;color:#4e6ab6}

