@import url("https://fonts.googleapis.com/css2?family=Mukta&display=swap");

body {
  font-family: "Mukta", sans-serif;
  background-color: rgb(244, 253, 255);
}

.hidden {
  display: none;
}

ul {
  list-style-type: none;
}

.buttons {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.button {
  font-family: "Mukta", sans-serif;
  padding: 3px 20px;
  background-color: rgb(189, 255, 216);
  border: none;
  border-radius: 20px;
  color: rgb(78, 182, 121);
  outline: none;
  box-shadow: rgba(189, 255, 216, 0.5) 0 7px 29px 0;
  transition: all 0.5s;

  &:hover {
    background-color: rgb(189, 218, 255);
    box-shadow: rgba(189, 242, 255, 0.5) 0 7px 29px 0;
    color: rgb(78, 106, 182);

  }
}

select {
  background-color: rgb(189, 255, 216);
  border: none;
  border-radius: 20px;
  outline: none;
  color: rgb(78, 182, 121);
  padding: 3px 20px;
}

.listOfGoods {
  display: flex;
  align-items: center;
  flex-direction: column;
  color: rgb(78, 106, 182);
}
